import React, { Component } from 'react';
import { AuthContent,RightAlignedLink,AuthError} from '../../components/Auth';
import  './Login.scss';
import { connect } from 'react-redux';
import {bindActionCreators} from 'redux';
import * as userActions from '../../redux/modules/user';
import * as authActions from '../../redux/modules/auth';
import storage from '../../lib/storage';


class Login extends Component {

    componentWillUnmount() {
        const { AuthActions } = this.props;
        AuthActions.initializeForm('login');
    }
    setError = (message) => {
        const { AuthActions } = this.props;
        AuthActions.setError({
            form : 'login',
            message
        });
        return false;
    }
    
    handleChange = (e) => {
        const { AuthActions } = this.props;
        const { name, value} =e.target;

        AuthActions.changeInput({
            name,
            value,
            form:'login'
        });
    }
    handleLocalLogin = async () => {
        const { form,AuthActions,UserActions,history } = this.props;
        const { email, password } = form.toJS();

        try {
            await AuthActions.localLogin({email,password});
            const loggedInfo = this.props.result.toJS();
            UserActions.setLoggedInfo(loggedInfo);
            history.push('/'); //Î©îÏù∏ÏúºÎ°ú ÎêòÎèåÎ¶º
            storage.set('loggedInfo',loggedInfo);
            
        } catch(e) {
            if(e.response.status === 402) {
                    return this.setError('Ïú†Ìö®ÌïòÏßÄ ÏïäÎäî Ïù¥Î©îÏùºÏûÖÎãàÎã§.');
            }
            else if(e.response.status ===403){
                return this.setError('Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÏïîÌò∏ÏûÖÎãàÎã§.');
            }
            else{
                return this.setError('ÏïåÏàòÏóÜÎäî Ï†ïÎ≥¥ÏûÖÎãàÎã§.')
            }
      
        }
    }
    
    render() {
        const { email, password } = this.props.form.toJS(); //formÏóêÏÑú emailÍ≥º passwordÎ•º ÏùΩÏñ¥Ïò®Îã§.
        const { error } = this.props;
        const { handleChange ,handleLocalLogin}= this;
        return (
            <AuthContent title="Login">
            <input name="email" placeholder="E-mail" value={email} onChange={handleChange}/>
            <input name="password" placeholder="Password" value={password}type="password"
            onChange={handleChange}/>
            {
                error && <AuthError>{error}</AuthError>
            }
            <button onClick={handleLocalLogin}>Login</button>
            <RightAlignedLink to="/auth/register/local">Sign up?</RightAlignedLink>
            </AuthContent>
        );
    }
}

export default connect(

    //propsÍ∞íÏúºÎ°ú ÎÑ£Ïñ¥ Ï§Ñ ÏÉÅÌÉúÎ•º Ï†ïÏùòÌï¥Ï§ÄÎã§.
    (state) => ({
        form : state.auth.getIn(['login','form']), //login ÏïàÏùò form Í∞í
        error: state.auth.getIn(['login','error']),
        result: state.auth.get('result')
    }),
//propsÍ∞íÏúºÎ°ú ÎÑ£Ïñ¥Ï§Ñ Ïï°ÏÖò Ìï®ÏàòÎ•º Ï†ïÏùò Ìï¥Ï§ÄÎã§.
    (dispatch) => ({
        AuthActions : bindActionCreators(authActions,dispatch),
        UserActions : bindActionCreators(userActions,dispatch)
    })
)(Login);