import { createAction, handleActions } from 'redux-actions';
import { pender} from 'redux-pender';
import * as AuthAPI from '../../lib/auth';
import { Map } from 'immutable';

//Ïï°ÏÖò ÌÉÄÏûÖÏ†ïÏùò
const CHANGE_INPUT = 'auth/CHANGE_INPUT';  //input Í∞í Î≥ÄÍ≤Ω
const INITIALIZE_FORM = 'auth/INITIALIZE_FORM';//formÍ∞í  Ï¥àÍ∏∞Ìôî
const CHECK_EMAIL_EXISTS = 'auth/CHECK_EMAIL_EXISTS'; //Ïù¥Î©îÏùº Ï§ëÎ≥µÌôïÏù∏
const CHECK_USERNAME_EXISTS = 'auth/CHECK_USERNAME_EXISTS';//ÏïÑÏù¥Îîî Ï§ëÎ≥µ ÌôïÏù∏
const LOCAL_REGISTER = 'auth/LOCAL_REGISTER';// ÌöåÏõê Í∞ÄÏûÖ
const LOCAL_LOGIN= 'auth/LOCAL_LOGIN'; //Î°úÍ∑∏Ïù∏
const LOGOUT='auth/LOGOUT';//Î°úÍ∑∏ÏïÑÏõÉ
const SET_ERROR = 'auth/SET_ERROR'; //Ïò§Î•ò ÏÑ§Ï†ï 
//Ïï°ÏÖò ÏÉùÏÑ±Ìï®Ïàò ÏÉùÏÑ±
export const checkEmailExists = createAction(CHECK_EMAIL_EXISTS,AuthAPI.checkEmailExists);
export const checkUsernameExists = createAction(CHECK_USERNAME_EXISTS,AuthAPI.checkUsernameExists);
export const changeInput = createAction(CHANGE_INPUT);
export const initializeForm = createAction(INITIALIZE_FORM);
export const localRegister = createAction(LOCAL_REGISTER,AuthAPI.localRegister);
export const localLogin = createAction(LOCAL_LOGIN,AuthAPI.localLogin); 
export const logout = createAction(LOGOUT,AuthAPI.logout);
export const setError = createAction(SET_ERROR);
const initialState = Map({
    register: Map({
        error:'',
        form: Map({
            email: '',
            username: '',
            password: '',
            passwordConfirm : ''
        }),
        exists: Map({
            email: false,
            password : false,
            username: false
        }),
        
    }),
    login: Map({
        form: Map({
            email: '',
            password: ''
        }),
        error:''
    }),
    result : Map ({})
});

//handleactionÏùò Ï≤´ Î≤àÏß∏ ÌååÎùºÎØ∏ÌÑ∞Îäî Ïï°ÏÖòÏùÑ Ï≤òÎ¶¨ÌïòÎäî Ìï®ÏàòÎì§Î°ú Ïù¥Î£®Ïñ¥ÏßÑ Í∞ùÏ≤¥,
//ÎëêÎ≤àÏß∏Îäî Ï¥àÍ∏∞ÏÉÅÌÉú
export default handleActions({
    [CHANGE_INPUT] : (state,action) => {
        const {form, name, value } = action.payload;
        return state.setIn([form, 'form',name],value);
    },
    [INITIALIZE_FORM] : (state,action) => {
        const initialForm = initialState.get(action.payload);
        return state.set(action.payload, initialForm);
    },
    [SET_ERROR] : (state,action) => {
        const {form,message} = action.payload;
        return state.setIn([form, 'error'],message);
    },
    ...pender({
        type: CHECK_EMAIL_EXISTS,
        onSuccess: (state, action) => state.setIn(['register','exists','email'],action.payload.data.exists)
    }),
    ...pender({
        type:CHECK_USERNAME_EXISTS,
        onSuccess: (state, action) => state.setIn(['register','exists','username'],action.payload.data.exists)
    }),
    ...pender({
        type:LOCAL_LOGIN,
        onSuccess : (state,action) => state.set('result',Map(action.payload.data))
    }),
    ...pender({
        type:LOCAL_REGISTER,
        onSuccess : (state,action) => state.set('result',Map(action.payload.data))
    })
},initialState);