//Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóê json ÌòïÌÉúÎ°ú Ï†ÄÏû• /Î∂àÎü¨Ïò§Í∏∞ /ÏÇ≠Ï†ú Î•º ÎèÑÏôÄÏ£ºÎäî Ìó¨Ìçº
const storage = {
    set: (key, object) => {
        if(!localStorage) return;
        localStorage[key] = (typeof object) === 'string' ? object : JSON.stringify(object);
        //vÌååÎùºÎØ∏ÌÑ∞Í∞Ä Í∞ùÏ≤¥ÌòïÏãùÏùºÏãú ÏûêÎèôÏúºÎ°ú JSON.stringify ÏúºÎ°ú Î∞îÍøîÏ§å.
        //ÏùºÎ∞òÎ¨∏ÏûêÏó¥ÏùºÏãú Í∑∏ÎåÄÎ°ú ÎÑ£Ïñ¥Ï§å .
    },
    get: (key) => {
    if(!localStorage) return null;
        //Î°úÏª¨Ïä§ÌÜ†ÎùºÏßÄÍ∞Ä ÏóÜÏúºÎ©¥ null
    if(!localStorage[key]){
        return null;
    }
    
    try{
        const parsed = JSON.parse(localStorage[key]); //jsonÏúºÎ°ú Î≥ÄÌôò
        return parsed;
    } catch(e) {
        return localStorage[key];
    }
    },
    remove : (key) => {
        if(!localStorage) return null;

        if(localStorage[key]){
            localStorage.removeItem(key); //ÏïÑÏù¥ÌÖú Ï†úÍ±∞
        }
    }
    };

    export default storage;
