import React, { Component } from 'react';
import { AuthContent,RightAlignedLink,AuthError } from '../../components/Auth';
import './Register.scss';
import {connect} from 'react-redux';
import {bindActionCreators } from 'redux';
import * as authActions from '../../redux/modules/auth';
import * as userActions from '../../redux/modules/user';
import {isEmail, isLength, isAlphanumeric } from 'validator';
import storage from '../../lib/storage';

class Register extends Component {
    componentWillUnmount() {
        const { AuthActions } = this.props;
        AuthActions.initializeForm('register')
    }

    setError = (message) => { 
        const {AuthActions } = this.props;
        AuthActions.setError({
            form : 'register',
            message 
        });
    }

    validate = {
        email : (value) => {
            if(!isEmail(value)) {
                this.setError('ÏûòÎ™ªÎêú Ïù¥Î©îÏùº ÌòïÏãù ÏûÖÎãàÎã§.');
                return false;
            }
            return true;
        },
        username: (value) => {
            if(!isAlphanumeric(value) || !isLength(value, {min:4, max:15})) {
                this.setError('ÏïÑÏù¥ÎîîÎäî 4~15Í∏ÄÏûêÏùò ÏïåÌååÎ≤≥ ÌòπÏùÄ Ïà´ÏûêÎ°ú Ïù¥Î£®Ïñ¥Ï†∏Ïïº Ìï©ÎãàÎã§.');
               return false;
            }
              return true;
        },
        password: ( value) => {
            if(!isLength(value, {min:6 })) {
                this.setError('ÎπÑÎ∞ÄÎ≤àÌò∏Îäî 6Ïûê Ïù¥ÏÉÅÏûÖÎãàÎã§.');
                return false;
            }
            this.setError(null);
            return true;
        },
        passwordConfirm : (value) => {
            if(this.props.form.get('password') !== value) {
                this.setError('ÎπÑÎ∞ÄÎ≤àÌò∏ÌôïÏù∏Ïù¥ ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.');
                return false;
            }
            this.setError(null);
            return true;
        }
    };



    checkEmailExists = async(email)=>{
        const { AuthActions } = this.props;
        try{
            await AuthActions.checkEmailExists(email);
            if(this.props.exists.get('email')){
                this.setError('Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î©îÏùºÏûÖÎãàÎã§.');
            } else{
                this.setError(null);
            }
        }catch(e) {
            console.log(e);
        }
    };
    checkUsernameExists = async(username) =>{
        const {AuthActions } = this.props;
        try{
            await AuthActions.checkUsernameExists(username);
            if(this.props.exists.get('username')){
                this.setError('Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§.');
            } else{
                this.setError(null);
            }
        }catch(e) {
            console.log(e);
        }
    };



    handleChange = (e) => {
        const {AuthActions } = this.props;
        const { name, value } = e.target;

        AuthActions.changeInput({
            name,
            value,
            form: 'register'
        });
        /*
        const validation = this.validate[name](value);
        if(name.indexOf('password') > -1 || !validation) return ; 
        //ÎπÑÎ∞ÄÎ≤àÌò∏ Í≤ÄÏ¶ùÏù¥Í±∞ÎÇò, Í≤ÄÏ¶ù Ïã§Ìå®Ïãú Ïó¨Í∏∞ÏóêÏÑú ÎÅù ÎßàÏπ®.
        
            //Ïù¥Î©îÏùº, ÏïÑÏù¥Îîî Ï§ëÎ≥µ ÌôïÏù∏`
                const check = name === 'email' ? this.checkEmailExists : this.checkUsernameExists; 
                check(value);
*/
    }
handleLocalRegister = async () => {
    const { form, AuthActions,UserActions,error,history } = this.props;
    const { email, username ,password, passwordConfirm} = form.toJS();

    const { validate } =this;
    
    validate['email'](email);
    validate['username'](username);
    validate['password'](password);
    validate['passwordConfirm'](passwordConfirm);

    if(error) return ; //ÏóêÎü¨Í∞Ä ÏûàÏùÑÏãú Î¶¨ÌÑ¥Ìï®
    if(!validate['email'](email)
    || !validate['username'](username)
    || !validate['password'](password)
    ||!validate['passwordConfirm'](passwordConfirm)){
        //ÌïòÎÇòÎùºÎèÑ Ïã§Ìå®Ìï†Ïãú ÏßÑÌñâÌïòÏßÄ ÏïäÎäîÎã§.
                return ;
    }
    


try { 
    await AuthActions.localRegister({
        email, username,password
    });
    const loggedInfo = this.props.result.toJS();
    storage.set('loggedInfo',loggedInfo);
    UserActions.setLoggedInfo(loggedInfo);
    UserActions.setValidated(true);
    //Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ Ï†ÄÏû•
    history.push('/Auth/login/local'); //ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µÏãú ÌôàÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô 
} catch(e) {
    if(e.response.status === 409 ) {
        const { key } = e.response.data ;
        const message = key ==='email' ? ' Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïù¥Î©îÏùºÏûÖÎãàÎã§ ' : 'Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî Ïú†Ï†ÄÎÑ§ÏûÑÏûÖÎãàÎã§';
        return this.setError(message);
    }
  
    }
};

    render() {
        const { error } = this.props;
        const { email, username, password, passwordConfirm } = this.props.form.toJS();
        const { handleChange,handleLocalRegister } = this;
        return (
            <AuthContent title="Register">
            <input name="email" placeholder="E-mail" value={email} onChange={handleChange}/>
            <input name="username" placeholder="Username" value={username}  onChange={handleChange}/>
            <input name="password" placeholder="Password" type="password" value={password}  onChange={handleChange}/>
            <input name="passwordConfirm" placeholder="Confirm Password" type="password" value={passwordConfirm}  onChange={handleChange}/> 
            {
                error && <AuthError>{error}</AuthError>
            }
            <button onClick={handleLocalRegister}>Sign up</button>
            <RightAlignedLink to="/auth/login/local">Login?</RightAlignedLink>
         
            </AuthContent>
        );
    }
}

export default connect(
    (state) => ({
        form: state.auth.getIn(['register', 'form']),
        error : state.auth.getIn(['register','error']),
        exists : state.auth.getIn(['register','exists']),
        result : state.auth.get('result')
    }),
    (dispatch) => ({
        AuthActions: bindActionCreators(authActions, dispatch),
        UserActions :bindActionCreators(userActions,dispatch)
    })
)(Register);