import { createAction, handleActions } from 'redux-actions';
import { Map } from 'immutable';
import * as AuthAPI from '../../lib/auth';
import { pender } from 'redux-pender';

const SET_LOGGED_INFO = 'user/SET_LOGGED_INFO'; //Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ ÏÑ§Ï†ï
const SET_VALIDATED = 'user/SET_VALIDATED'; // validated Í∞í ÏÑ§Ï†ï
const LOGOUT = 'user/LOGOUT'; //Î°úÍ∑∏ ÏïÑÏõÉ
const CHECK_STATUS = 'user/CHECK_STATUS' ; //ÌòÑÏû¨ Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú ÌôïÏù∏

export const setLoggedInfo = createAction(SET_LOGGED_INFO); // loggedInfo 
export const setValidated = createAction(SET_VALIDATED); // validated
export const logout = createAction(LOGOUT, AuthAPI.logout); 
export const checkStatus = createAction(CHECK_STATUS,AuthAPI.checkStatus);

const initialState = Map({
    loggedInfo : Map ({ //ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Ï§ëÏù∏ Ïú†Ï†ÄÏùò Ï†ïÎ≥¥
            username: null
    }),
    logged: false , //ÌòÑÏû¨ Î°úÍ∑∏Ïù∏Ï§ëÏù∏ÏßÄ ÏïåÎ†§Ï§ÄÎã§.
    validated: false //Î°úÍ∑∏Ïù∏ Ï§ëÏù∏ÏßÄ ÏïÑÎãåÏßÄ ÌïúÎ≤à ÏÑúÎ≤ÑÏ∏°Ïóê Í≤ÄÏ¶ùÏùÑ ÌñàÎã§ÎäîÍ±∏ ÏùòÎØ∏    
});

export default handleActions({
    [SET_LOGGED_INFO] : (state, action) => state.set('loggedInfo',Map(action.payload)).set('logged',true),
    [SET_VALIDATED] : (state,action) => state.set('validated',action.payload),
    ...pender({
        type: CHECK_STATUS,
        onSuccess : (state,action) => state.set('loggedInfo',Map(action.payload.data)).set('validated',true),
        onFailure : (state,action) => initialState
    })
},initialState);